import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable()
export class ProductStockService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getProductStockList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/product_stock/list`, { params: param });
  }

  getProductStockSummeryList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/product_stock/summary-list`, { params: param });
  }

}
